* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Karla', sans-serif;
}

body {
    background-color: #060d2c;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.game-container {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;

    height: 500px;
    min-width: 500px;
    width: 50vw;
    border-radius: 10px;

    background-color: #ebe4c5;
}

.game-container > h1 {
    font-weight: 600px;
    font-size: 36px;
}

.settings > input {
    margin-right: 4px;
}

.settings > label {
    user-select: none;
}

.game-instructions {
    width: 80%;
    text-align: center;
    line-height: 24px;
}

.dice-container {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: 1fr 1fr;
    gap: 24px;
    justify-items: center;
    align-items: center;
}

.score-container {
    font-size: 24px;
    font-weight: 600;
    margin: -8px auto;
}

button {
    font-size: 24px;
    font-weight: 600;
    width: 150px;
    height: 48px;
    border: none;
    border-radius: 5px;
    color: #ffffff;
    background-color: #0025c9;
    cursor: pointer;
    margin-top: 16px;
}

button:active {
    box-shadow: inset 2px 2px 8px 0px #222;
}